import {
  required,
  minLength,
  maxLength,
  email,
  sameAs,
} from 'vuelidate/lib/validators';

import ProgressBar from './BaseStepProgressBar.vue';
import ColorLine from './ColorLine.vue';
import BaseButton from './BaseButton.vue';

export default {
  name: 'RecoverStepper',
  components: {
    ProgressBar,
    ColorLine,
    BaseButton,
  },
  data() {
    return {
      currentStep: 1,
      count: 0,
      numberOfSteps: 2,
      stepsTitle: ['', ''],
      email: null,
    };
  },
  validations: {
    email: {
      required,
      email,
      maxLength: maxLength(30),
    },
    password: {
      required,
      minLength: minLength(8),
      maxLength: maxLength(15),
    },
    confirmPassword: {
      required,
      sameAsPassword: sameAs('password'),
    },
  },
  computed: {
    emailErrorMessage() {
      if (!this.$v.email.required) {
        return 'Email é requerido';
      }
      if (!this.$v.email.email) {
        return 'Por favor insira um email válido';
      }
      return '';
    },
  },
  methods: {
    toHome() {
      this.$router.push({ name: 'Home' });
    },
    cancel() {
      this.$router.push({ name: 'SignIn' });
    },
    nextStep() {
      if (this.count <= (this.numberOfSteps)) {
        this.currentStep += 1;
        this.count += 1;
      }
      this.scrollToTop();
    },
    prevStep() {
      if (this.count >= 0) {
        this.currentStep -= 1;
        this.count -= 1;
      }
      this.scrollToTop();
    },
    scrollToTop() {
      window.scrollTo({
        top: 0,
        left: 0,
        behavior: 'smooth',
      });
    },
  },
};
